import { createSlice }  from  "@reduxjs/toolkit";

const movieSlice=createSlice({
    name:"movies",
    initialState:{
        nowPlayingMovies:null,
        trailerVideo:null,
        popularMovies:null, 
        topRatedMovies:null,
        upcomingMovies:null,
        selectedMovies:null,
        selectedMoviesDetails:null,
        selectedMoviesCast:null,
        selectedMoviesTrailer:null
    },
    reducers:{
        addNowPlayingMovies:(state,action)=>{
            state.nowPlayingMovies=action.payload;
        },
        addPopularMovies:(state,action)=>{
            state.popularMovies=action.payload;
        },
        addTopRatedMovies:(state,action)=>{
            state.topRatedMovies=action.payload;
        },
        addUpcomingMovies:(state,action)=>{
            state.upcomingMovies=action.payload;
        },
        addTrailerVideo:(state,action)=>{
            state.trailerVideo=action.payload;
        },
        addSelectedMovies:(state,action)=>{
            state.selectedMovies=action.payload;
        },
        addselectedMoviesDetails:(state,action)=>{
            state.selectedMoviesDetails=action.payload
        },
        addSelectedMoviesCast:(state,action)=>{
            state.selectedMoviesCast=action.payload;
        },
        addSelectedMoviesTrailer:(state,action)=>{
            state.selectedMoviesTrailer=action.payload
        }
    }
})
export const{addSelectedMoviesCast,addNowPlayingMovies,addTrailerVideo,addPopularMovies,addTopRatedMovies,addUpcomingMovies,addSelectedMovies,addselectedMoviesDetails,addSelectedMoviesTrailer}=movieSlice.actions;
export default movieSlice.reducer;